import { createStore } from 'vuex'
import questionList from './assets/questionList'

const store = createStore({
  state(){
    return {
      name:"kimjiwon",
      age:20,
      page:0,
      question:[],
      // 질문 선택 저장
      questionList:questionList,
      // 질문지목록 questionList.js파일
      total:0,
      btnClick:true,
      // totalOnce:true,
      questionPage:1,
      // 테스트 페이지 문제페이지
    }
  },
  mutations:{
    // 변경함수
    addAge(state){
        state.age++;
    },
    questionAdd(state,a){
      // state랑 payload
      // state.question.push(a);
      state.total+=a;
      // if(state.totalOnce==true){
      //   // 버튼 중복클릭 방지
      //   state.total+=a;
      //   state.totalOnce=false;
      // }
      
      // console.log("문제별기록",state.question);
      console.log("총점수",state.total);
    },
    changePage(state,a){
      state.page = a;
      if(a==0){
        // 다시시작하면 초기화
        state.question=[];
        state.total=0;
        state.questionPage=1;
        console.log('=====다시하기====')
      }
    },
    nextQuestion(state){
      if(state.questionPage<21){
        state.questionPage+=1;
      }
    },
  }
})

export default store